@import '../../../../../../../../../../../helpers/_variables';

.artist-image {
  position: relative;
  margin-right: $margin;

  &__photo {
    width: 150px;
    height: 150px;

    border-radius: 50%; }

  &__authorized {
    position: absolute;
    bottom: 5px;
    right: 25px;
    text-align: center;

    width: 20px;
    height: 20px;

    color: $white;

    background-color: $blue;
    border-radius: 50%; } }
