@import '../../../../helpers/_variables';

.action-button {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  padding: 0 $padding;

  height: 27px;

  font-size: 11px;
  font-weight: 500;
  line-height: 11px;
  color: $grey;
  text-transform: uppercase;
  letter-spacing: 1px;

  outline: none;
  background: none;

  cursor: pointer;

  &__play {
    margin-right: $margin-sm;

    color: $white;

    background: $green;
    border: 1px solid $green;
    border-radius: 20px;

    &:hover {
      filter: brightness(108%); } }

  &__play-icon {
    display: flex;

    margin-right: $margin-xs; }

  &__follow {
    margin-right: $margin-sm;

    color: $sand;

    border: 1px solid $sand;
    border-radius: 20px;

    &:hover {
      color: $white;

      border-color: $white; } }

  &__more {
    text-align: center;

    width: 27px;
    height: 27px;
    padding: 0 0 0 4px;

    color: $sand;

    border: 1px solid $sand;
    border-radius: 50%;

    &:hover {
      color: $white;

      border-color: $white; } }

  &__save {
    margin-right: $margin-sm;
    padding: 0 $padding-lg;

    color: $sand;

    border: 1px solid $sand;
    border-radius: 20px;

    &:hover {
      color: $white;

      border-color: $white; } }

  &__find-friends {
    color: $sand;

    border: 1px solid $sand;
    border-radius: 20px;

    &:hover {
      color: $white;

      border-color: $white; } }

  &__5-more {
    color: $sand;

    border: 1px solid $sand;
    border-radius: 20px;

    &:hover {
      color: $white;

      border-color: $white; } } }
