@import '../../../../../../helpers/_variables';

.tab-item {
  text-align: center;
  flex-grow: 1;

  width: 100%;
  padding: $padding 0;

  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: $grey;

  background: none;
  border: none;
  outline: none;

  cursor: pointer;

  transition: color $hover-transition-duration ease;

  &:hover {
    color: $white; }

  &_active {
    color: $white;

    cursor: default; } }
